import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import * as React from "react";
import { Link } from "react-router-dom";
import girlo from "../girlo.jpg";
import { Header } from "./header";

function handleChangeName(
  event: React.ChangeEvent<HTMLInputElement>,
  setName: Function,
  setCorrect: Function,
) {
  if (
    event.target.value.toLowerCase().includes("me") ||
    event.target.value.toLowerCase().includes("lexi")
  ) {
    setCorrect(true);
  }
  setName(event.target.value);
}

export function PageNine() {
  const [name, setName] = React.useState("");
  const [correct, setCorrect] = React.useState(false);
  return (
    <div>
      <Header></Header>
      <p>
        Question 9: Who's the bestest most wonderfulest girlo I could ever ask
        for?
      </p>
      <p>
        <TextField
          id="outlined-controlled"
          value={name}
          onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
            handleChangeName(event, setName, setCorrect);
          }}
        />
      </p>
      {correct && (
        <div>
          <p>
            <Button component={Link} to="/prizePage" variant="contained">
              Prize Selection
            </Button>
          </p>
          <p>
            <img src={girlo} height={500} width={400}></img>
          </p>
        </div>
      )}
    </div>
  );
}
