import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import * as React from "react";
import { Link } from "react-router-dom";
import bus from "../bus.jpg";
import { Header } from "./header";

function handleChangeName(
  event: React.ChangeEvent<HTMLInputElement>,
  setName: Function,
  setCorrect: Function,
) {
  if (event.target.value.toLowerCase().includes("bus")) {
    setCorrect(true);
  }
  setName(event.target.value);
}

export function PageSeven() {
  const [name, setName] = React.useState("");
  const [correct, setCorrect] = React.useState(false);
  return (
    <div>
      <Header></Header>
      <p>Question 7: In what vehicle did we tour london in?</p>
      <p>
        <TextField
          id="outlined-controlled"
          value={name}
          onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
            handleChangeName(event, setName, setCorrect);
          }}
        />
      </p>
      {correct && (
        <div>
          <p>
            <Button component={Link} to="/page8" variant="contained">
              Next Question :)
            </Button>
          </p>
          <p>
            <img src={bus} height={400} width={500}></img>
          </p>
        </div>
      )}
    </div>
  );
}
