import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import * as React from "react";
import { Link } from "react-router-dom";
import willow from "../willow.jpg";
import { Header } from "./header";

function handleChangeName(
  event: React.ChangeEvent<HTMLInputElement>,
  setName: Function,
  setCorrect: Function,
) {
  if (event.target.value.toLowerCase().includes("willow")) {
    setCorrect(true);
  }
  setName(event.target.value);
}

export function PageThree() {
  const [name, setName] = React.useState("");
  const [correct, setCorrect] = React.useState(false);
  return (
    <div>
      <Header></Header>
      <p>
        Question 3: What is the name of my dad's dog with the line on her nose?
      </p>
      <p>
        <TextField
          id="outlined-controlled"
          value={name}
          onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
            handleChangeName(event, setName, setCorrect);
          }}
        />
      </p>
      {correct && (
        <div>
          <p>
            <Button component={Link} to="/page4" variant="contained">
              Next Question :)
            </Button>
          </p>
          <p>
            <img src={willow} height={500} width={400}></img>
          </p>
        </div>
      )}
    </div>
  );
}
