import Button from "@mui/material/Button";
import * as React from "react";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import Select, { SelectChangeEvent } from "@mui/material/Select";
import { Header } from "./header";
import niallHoran from "../niallHoran.jpg";
import massage from "../couplesMessage.jpg";
import { Link } from "react-router-dom";

export function PrizePage() {
  const [selected, setSelected] = React.useState<string>("");
  const [open, setOpen] = React.useState(false);
  const [submitted, setSubmitted] = React.useState(false);
  const [submittedAnswer, setSubmittedAnswer] = React.useState<string>("");

  const handleChange = (event: SelectChangeEvent<typeof selected>) => {
    setSelected(event.target.value);
  };

  const submitAnswer = () => {
    setSubmitted(true);
    setSubmittedAnswer(selected);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleOpen = () => {
    setOpen(true);
  };
  const selectedNiall = submittedAnswer === "niall" && submitted;
  const selectedMassage = submittedAnswer === "massage" && submitted;

  return (
    <div>
      <Header></Header>

      <p>
        <InputLabel id="demo-controlled-open-select-label">
          I hope you enjoyed the treasure hunt/quiz. Please pick a prize.
        </InputLabel>
        <Select
          sx={{ m: 1, minWidth: 500 }}
          size="medium"
          labelId="demo-controlled-open-select-label"
          id="demo-controlled-open-select"
          open={open}
          onClose={handleClose}
          onOpen={handleOpen}
          value={selected}
          onChange={handleChange}
          disabled={submitted === true}
        >
          <MenuItem value={"niall"}>Two Tickets to Niall Horan</MenuItem>
          <MenuItem value={"massage"}>
            Couples Massage for Two at Salon of Your Choice
          </MenuItem>
        </Select>
      </p>
      <p>
        <Button
          onClick={submitAnswer}
          variant="contained"
          disabled={selected === "" || submitted === true}
        >
          Submit
        </Button>
      </p>
      {selectedNiall && (
        <div>
          <p>Good choice! I'll try to remember the date this time.</p>
          <p>
            <img src={niallHoran} height={400} width={400}></img>
          </p>
        </div>
      )}
      {selectedMassage && (
        <div>
          <p>Good choice! This should be really relaxing :)</p>
          <p>
            <img src={massage} height={400} width={400}></img>
          </p>
        </div>
      )}
      {submitted && (
        <div>
          <p>
            <Button component={Link} to="/bonusPage" variant="contained">
              Surprise bonus question
            </Button>
          </p>
        </div>
      )}
    </div>
  );
}
