import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import * as React from "react";
import { Link } from "react-router-dom";
import house from "../house.jpg";
import { Header } from "./header";

function handleChangeName(
  event: React.ChangeEvent<HTMLInputElement>,
  setName: Function,
  setCorrect: Function,
) {
  if (
    event.target.value.toLowerCase().includes("217 roslyn ct") ||
    event.target.value.toLowerCase().includes("217 roslyn court")
  ) {
    setCorrect(true);
  }
  setName(event.target.value);
}

export function PageBonus() {
  const [name, setName] = React.useState("");
  const [correct, setCorrect] = React.useState(false);
  return (
    <div>
      <Header></Header>
      <p>
        Bonus Question: Would you like to live with me next year? If so, please
        enter my address.
      </p>
      <p>
        <TextField
          id="outlined-controlled"
          value={name}
          onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
            handleChangeName(event, setName, setCorrect);
          }}
        />
      </p>
      {correct && (
        <div>
          <p>
            <img src={house} height={400} width={500}></img>
          </p>
          <p>Was this promposal enough for you?</p>
          <p>
            <Button component={Link} to="/finalPage" variant="contained">
              Finish
            </Button>
          </p>
        </div>
      )}
    </div>
  );
}
